import React from "react";

const Card = (props) => {
    return (
        <>
      <div className = "col-md-4 col-10 mx-auto d-flex justify-content-center ">
     <div className="card">
  <img src={props.imgsrc} className="card-img-top" alt="..."/>
  <div className="card-body">
    <h5 className="card-title">{props.title}</h5>
    <p className="card-text">{props.text}</p>
    <p className = "card-text">{props.texts}</p>
  </div>
  </div>
  </div>
        </>
    );

};

export default Card;